.homepage {
    width: 100%;
    height: 80em;
    margin-top: -2.7em;
    background: rgba(0, 0, 0, 0);
}


.particles{
        position: fixed;
        right: 0;
        bottom: 0;
        min-width: 100%;
        min-height: 100%;
        z-index: -1 !important;
}

.nav {
   padding-bottom: 10em;
}


@-webkit-keyframes roundandround {
    to {
        -webkit-transform: rotateX(360deg) rotateY(360deg);
                transform: rotateX(360deg) rotateY(360deg);
    }
}


@keyframes roundandround {
    to {
        -webkit-transform: rotateX(360deg) rotateY(360deg);
                transform: rotateX(360deg) rotateY(360deg);
    }
}

@-webkit-keyframes show {
    to {
        opacity: 1;
    }
}

@keyframes show {
    to {
        opacity: 1;
    }
}

body {
    background-color: #000000;
}

.scene {
    width: 600px;
    height: 600px;
    margin: 2% auto;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

.wrapper {
    width: 100%;
    height: 100%;
    -webkit-transform: rotateX(45deg) rotateY(45deg);
            transform: rotateX(45deg) rotateY(45deg);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}

.ball {
    position: relative;
    width: 40%;
    height: 40%;
    margin: 0 auto;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: roundandround 7.5s 1.3s infinite linear;
            animation: roundandround 7.5s 1.3s infinite linear;
}

.ball .ring {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 6px;
    border-style: dashed;
    border-radius: 50%;
    opacity: 0;
    -webkit-animation: show 0.75s forwards ease-in-out;
            animation: show 0.75s forwards ease-in-out;
}

.ring:nth-child(1) {
    color: #8df435;
    -webkit-transform: rotateY(4deg);
            transform: rotateY(4deg);
    -webkit-animation-delay: 0s;
            animation-delay: 0s;
}

